// frontend/src/components/contact/ContactForm.tsx (version compl√®tement corrig√©e)
'use client';

import { useState } from 'react';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import * as z from 'zod';
import { FormField, Input, Textarea, Select, Checkbox } from '@/components/ui/Form';
import { Button } from '@/components/ui/Button';
import { Send } from 'lucide-react';
import { submitContactRequest, type ContactRequest } from '@/lib/laravel/client';

interface ContactFormProps {
  type: 'quote' | 'support' | 'partner' | 'career';
}

// Zod schemas
const baseSchema = z.object({
  type: z.enum(['quote', 'support', 'partner', 'career']),
  name: z.string().min(2, 'Le nom doit contenir au moins 2 caract√®res'),
  email: z.string().email('Adresse email invalide'),
  phone: z.string().optional(),
  message: z.string().min(10, 'Le message doit contenir au moins 10 caract√®res'),
  consent: z.boolean().refine(val => val === true, {
    message: 'Vous devez accepter la politique de confidentialit√©'
  })
});

const quoteSchema = baseSchema.extend({
  company: z.string().min(2, 'Le nom de l\'entreprise est requis'),
  service: z.enum(['iot', 'rh-ia', 'clientele', 'conciergerie', 'web', 'consulting']),
  budget: z.enum(['<5k', '5k-20k', '20k-50k', '50k-100k', '>100k']),
  timeline: z.enum(['urgent', '1-3months', '3-6months', '6-12months', 'flexible']),
});

const supportSchema = baseSchema.extend({
  service: z.enum(['iot', 'rh-ia', 'clientele', 'conciergerie', 'web', 'consulting']),
  priority: z.enum(['low', 'medium', 'high', 'critical']),
  contract_number: z.string().optional()
});

const partnerSchema = baseSchema.extend({
  company: z.string().min(2, 'Le nom de l\'entreprise est requis'),
  partner_type: z.enum(['technology', 'commercial', 'reseller', 'integration']),
  website: z.string().url('URL invalide').or(z.literal('')),
  company_size: z.enum(['1-10', '11-50', '51-200', '201-1000', '1000+'])
});

const careerSchema = baseSchema.extend({
  position: z.string().min(2, 'Le poste est requis'),
  linkedin: z.string().url('URL LinkedIn invalide').or(z.literal('')),
  portfolio: z.string().url('URL invalide').or(z.literal('')).optional(),
  resume: z.any().optional() // Added resume field for file upload
});

// Fonction pour les options de service
const getServiceOptions = () => [
  { value: 'iot', label: 'IoT & Hardware' },
  { value: 'rh-ia', label: 'RH & Intelligence Artificielle' },
  { value: 'clientele', label: 'Satisfaction client√®le' },
  { value: 'conciergerie', label: 'Conciergerie d\'entreprise' },
  { value: 'web', label: 'D√©veloppement Web & Mobile' },
  { value: 'consulting', label: 'Consulting Digital' }
];

const ContactForm = ({ type }: ContactFormProps) => {
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [submitSuccess, setSubmitSuccess] = useState(false);
  const [submitError, setSubmitError] = useState<string | null>(null);
  
  const schema = type === 'quote' ? quoteSchema :
                 type === 'support' ? supportSchema :
                 type === 'partner' ? partnerSchema :
                 careerSchema;
  
  type FormData = z.infer<typeof schema> & { type: typeof type };
  
  const { register, handleSubmit, formState: { errors }, reset } = useForm<FormData>({
    resolver: zodResolver(schema),
    defaultValues: {
      type,
      consent: false
    }
  });
  
  const onSubmit = async (data: FormData) => {
    setIsSubmitting(true);
    setSubmitError(null);
    
    try {
      // Convert data to match ContactRequest type
      const contactData: ContactRequest = {
        ...data,
        // Ensure all fields are properly typed
        type: data.type,
        name: data.name,
        email: data.email,
        phone: data.phone || '',
        message: data.message,
        consent: data.consent,
        // Add optional fields with type safety
        ...(type === 'quote' && {
          company: data.company,
          service: data.service,
          budget: data.budget,
          timeline: data.timeline
        }),
        ...(type === 'support' && {
          service: data.service,
          priority: data.priority,
          contract_number: data.contract_number
        }),
        ...(type === 'partner' && {
          company: data.company,
          partner_type: data.partner_type,
          website: data.website || '',
          company_size: data.company_size
        }),
        ...(type === 'career' && {
          position: data.position,
          linkedin: data.linkedin || '',
          portfolio: data.portfolio || '',
          resume: data.resume
        })
      };
      
      await submitContactRequest(contactData);
      setSubmitSuccess(true);
      reset();
      
      // Reset success message after 5 seconds
      setTimeout(() => setSubmitSuccess(false), 5000);
    } catch (error) {
      console.error('Error submitting form:', error);
      setSubmitError(
        error instanceof Error 
          ? error.message 
          : 'Une erreur est survenue. Veuillez r√©essayer.'
      );
    } finally {
      setIsSubmitting(false);
    }
  };

  return (
    <div className="max-w-4xl mx-auto">
      {submitSuccess && (
        <div className="mb-6 p-4 bg-green-50 border border-green-200 rounded-lg">
          <div className="flex items-center">
            <div className="w-8 h-8 bg-green-100 rounded-full flex items-center justify-center mr-3">
              <svg className="w-5 h-5 text-green-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M5 13l4 4L19 7" />
              </svg>
            </div>
            <div>
              <p className="font-medium text-green-800">Message envoy√© avec succ√®s !</p>
              <p className="text-sm text-green-700">Nous vous r√©pondrons dans les plus brefs d√©lais.</p>
            </div>
          </div>
        </div>
      )}
      
      {submitError && (
        <div className="mb-6 p-4 bg-red-50 border border-red-200 rounded-lg">
          <div className="flex items-center">
            <div className="w-8 h-8 bg-red-100 rounded-full flex items-center justify-center mr-3">
              <svg className="w-5 h-5 text-red-600" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 8v4m0 4h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
              </svg>
            </div>
            <div>
              <p className="font-medium text-red-800">Erreur</p>
              <p className="text-sm text-red-700">{submitError}</p>
            </div>
          </div>
        </div>
      )}
      
      <form onSubmit={handleSubmit(onSubmit)} className="space-y-6 bg-white p-6 rounded-lg shadow-sm border border-gray-200">
        {/* Common fields */}
        <div className="grid md:grid-cols-2 gap-6">
          <FormField label="Nom complet" error={errors.name?.message} required>
            <Input
              {...register('name')}
              placeholder="Votre nom"
              error={!!errors.name}
            />
          </FormField>
          
          <FormField label="Email" error={errors.email?.message} required>
            <Input
              type="email"
              {...register('email')}
              placeholder="votre@email.com"
              error={!!errors.email}
            />
          </FormField>
        </div>
        
        <FormField label="T√©l√©phone" error={errors.phone?.message}>
          <Input
            type="tel"
            {...register('phone')}
            placeholder="+229 01 23 45 67 89"
            error={!!errors.phone}
          />
        </FormField>
        
        {/* Type-specific fields */}
        {type === 'quote' && (
          <>
            <div className="grid md:grid-cols-2 gap-6">
              <FormField label="Entreprise" error={errors.company?.message} required>
                <Input
                  {...register('company')}
                  placeholder="Nom de votre entreprise"
                  error={!!errors.company}
                />
              </FormField>
              
              <FormField label="Service concern√©" error={errors.service?.message} required>
                <Select
                  {...register('service')}
                  options={getServiceOptions()}
                  error={!!errors.service}
                />
              </FormField>
            </div>
            
            <div className="grid md:grid-cols-2 gap-6">
              <FormField label="Budget estim√©" error={errors.budget?.message} required>
                <Select
                  {...register('budget')}
                  options={[
                    { value: '<5k', label: 'Moins de 5 000 ‚Ç¨' },
                    { value: '5k-20k', label: '5 000 - 20 000 ‚Ç¨' },
                    { value: '20k-50k', label: '20 000 - 50 000 ‚Ç¨' },
                    { value: '50k-100k', label: '50 000 - 100 000 ‚Ç¨' },
                    { value: '>100k', label: 'Plus de 100 000 ‚Ç¨' }
                  ]}
                  error={!!errors.budget}
                />
              </FormField>
              
              <FormField label="D√©lai souhait√©" error={errors.timeline?.message} required>
                <Select
                  {...register('timeline')}
                  options={[
                    { value: 'urgent', label: 'Urgent (moins d\'1 mois)' },
                    { value: '1-3months', label: '1-3 mois' },
                    { value: '3-6months', label: '3-6 mois' },
                    { value: '6-12months', label: '6-12 mois' },
                    { value: 'flexible', label: 'Flexible' }
                  ]}
                  error={!!errors.timeline}
                />
              </FormField>
            </div>
          </>
        )}
        
        {type === 'support' && (
          <>
            <FormField label="Num√©ro de contrat (optionnel)" error={errors.contract_number?.message}>
              <Input
                {...register('contract_number')}
                placeholder="CON-XXXX-XXXX"
                error={!!errors.contract_number}
              />
            </FormField>
            
            <div className="grid md:grid-cols-2 gap-6">
              <FormField label="Service concern√©" error={errors.service?.message} required>
                <Select
                  {...register('service')}
                  options={getServiceOptions()}
                  error={!!errors.service}
                />
              </FormField>
              
              <FormField label="Priorit√©" error={errors.priority?.message} required>
                <Select
                  {...register('priority')}
                  options={[
                    { value: 'low', label: 'Basse' },
                    { value: 'medium', label: 'Moyenne' },
                    { value: 'high', label: 'Haute' },
                    { value: 'critical', label: 'Critique' }
                  ]}
                  error={!!errors.priority}
                />
              </FormField>
            </div>
          </>
        )}
        
        {type === 'partner' && (
          <>
            <div className="grid md:grid-cols-2 gap-6">
              <FormField label="Entreprise" error={errors.company?.message} required>
                <Input
                  {...register('company')}
                  placeholder="Nom de votre entreprise"
                  error={!!errors.company}
                />
              </FormField>
              
              <FormField label="Type de partenariat" error={errors.partner_type?.message} required>
                <Select
                  {...register('partner_type')}
                  options={[
                    { value: 'technology', label: 'Partenariat technologique' },
                    { value: 'commercial', label: 'Partenariat commercial' },
                    { value: 'reseller', label: 'Revendeur' },
                    { value: 'integration', label: 'Int√©grateur' }
                  ]}
                  error={!!errors.partner_type}
                />
              </FormField>
            </div>
            
            <div className="grid md:grid-cols-2 gap-6">
              <FormField label="Site web" error={errors.website?.message}>
                <Input
                  type="url"
                  {...register('website')}
                  placeholder="https://example.com"
                  error={!!errors.website}
                />
              </FormField>
              
              <FormField label="Taille de l'entreprise" error={errors.company_size?.message} required>
                <Select
                  {...register('company_size')}
                  options={[
                    { value: '1-10', label: '1-10 employ√©s' },
                    { value: '11-50', label: '11-50 employ√©s' },
                    { value: '51-200', label: '51-200 employ√©s' },
                    { value: '201-1000', label: '201-1000 employ√©s' },
                    { value: '1000+', label: '1000+ employ√©s' }
                  ]}
                  error={!!errors.company_size}
                />
              </FormField>
            </div>
          </>
        )}
        
        {type === 'career' && (
          <>
            <FormField label="Poste souhait√©" error={errors.position?.message} required>
              <Input
                {...register('position')}
                placeholder="D√©veloppeur Full-Stack"
                error={!!errors.position}
              />
            </FormField>
            
            <div className="grid md:grid-cols-2 gap-6">
              <FormField label="LinkedIn" error={errors.linkedin?.message}>
                <Input
                  type="url"
                  {...register('linkedin')}
                  placeholder="https://linkedin.com/in/votre-profil"
                  error={!!errors.linkedin}
                />
              </FormField>
              
              <FormField label="Portfolio/GitHub" error={errors.portfolio?.message}>
                <Input
                  type="url"
                  {...register('portfolio')}
                  placeholder="https://github.com/votre-profil"
                  error={!!errors.portfolio}
                />
              </FormField>
            </div>
            
            <FormField label="CV (optionnel)">
              <div className="border-2 border-dashed border-gray-300 rounded-lg p-6 text-center hover:border-primary-500 transition-colors">
                <input
                  type="file"
                  {...register('resume')}
                  className="hidden"
                  id="resume-upload"
                  accept=".pdf,.doc,.docx"
                />
                <label htmlFor="resume-upload" className="cursor-pointer block">
                  <svg className="w-12 h-12 mx-auto text-gray-400 mb-3" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M7 16a4 4 0 01-.88-7.903A5 5 0 1115.9 6L16 6a5 5 0 011 9.9M15 13l-3-3m0 0l-3 3m3-3v12" />
                  </svg>
                  <p className="text-gray-600">Glissez-d√©posez votre CV ou cliquez pour parcourir</p>
                  <p className="text-sm text-gray-500 mt-1">PDF, DOC, DOCX (max 10MB)</p>
                </label>
              </div>
            </FormField>
          </>
        )}
        
        {/* Message */}
        <FormField 
          label="Message" 
          error={errors.message?.message} 
          required
          helpText={type === 'quote' ? 'D√©crivez votre projet en d√©tail pour un devis pr√©cis' : undefined}
        >
          <Textarea
            {...register('message')}
            placeholder={
              type === 'quote' ? 'D√©crivez votre projet, vos objectifs et vos contraintes...' :
              type === 'support' ? 'D√©crivez votre probl√®me technique en d√©tail...' :
              type === 'partner' ? 'D√©crivez votre proposition de partenariat...' :
              'Parlez-nous de vous, de votre exp√©rience et de vos motivations...'
            }
            rows={6}
            error={!!errors.message}
          />
        </FormField>
        
        {/* Consent */}
        <FormField error={errors.consent?.message}>
          <Checkbox
            {...register('consent')}
            label={
              <span className="text-gray-700">
                J'accepte la{' '}
                <a href="/legal/privacy" className="text-primary-600 hover:underline">
                  politique de confidentialit√©
                </a>
                {' '}et autorise VIBECRO √† traiter mes donn√©es personnelles.
              </span>
            }
            error={!!errors.consent}
          />
        </FormField>
        
        {/* Submit */}
        <div className="pt-4">
          <Button
            type="submit"
            variant="primary"
            size="lg"
            fullWidth
            disabled={isSubmitting}
            className="flex items-center justify-center gap-2"
          >
            {isSubmitting ? (
              <>
                <div className="w-5 h-5 border-2 border-white border-t-transparent rounded-full animate-spin"></div>
                <span>Envoi en cours...</span>
              </>
            ) : (
              <>
                <span>
                  {type === 'quote' ? 'Demander un devis' :
                   type === 'support' ? 'Envoyer ma demande' :
                   type === 'partner' ? 'Soumettre la demande' :
                   'Postuler maintenant'}
                </span>
                <Send className="w-5 h-5" />
              </>
            )}
          </Button>
          
          <p className="text-center text-sm text-gray-500 mt-4">
            R√©ponse garantie sous 4 heures ouvrables
          </p>
        </div>
      </form>
    </div>
  );
};

export default ContactForm;